body {
	font-family: 'Montserrat', sans-serif;
	line-height: 1.4;
	color: #131313;
	@include css-lock(14, 18, 600, 1000); }

img {
	display: inline-block;
	max-width: 100%;
	height: auto; }

a {
	text-decoration: none; }

.bg-gray {
	background-color: #f9f9fc; }

.btn {
	border-radius: 10px;
	font-size: 18px;
	color: #fff;
	padding: 29px 10px;
	min-width: 230px;
	font-weight: 700;
	font-family: 'Montserrat', sans-serif;
	position: relative;
	overflow: hidden;
	border: none;
	&:before {
		z-index: 1;
		position: absolute;
		width: 173px;
		height: 173px;
		border-radius: 50%;
		background-color: transparent;
		right: -90px;
		top: -130px; }
	@include r(767) {
		font-size: 14px;
		padding: 23px 10px; } }

.btn:focus {
	box-shadow: none; }

.btn:not(:disabled):not(.disabled).active:focus, .btn:not(:disabled):not(.disabled):active:focus, .show>.btn.dropdown-toggle:focus {
	box-shadow: none; }

.btn-primary:before {
	content: '';
	background-color: rgba(#f5f4fa, 0.13); }

.btn-light {
	color: $primary;
	&:hover {
		color: $primary; } }

.btn-light:before {
	content: '';
	background-color: rgba(#fb3f3f, 0.13);
	top: 30px; }


.form-control {
	color: #ffffff;
	font-size: 18px;
	border-radius: 10px;
	border: none;
	padding: 20px 40px;
	max-width: 260px;
	&:focus {
		color: #fff;
		border: none;
		box-shadow: none; }
	@include placeholder {
		font-size: 18px;
		color: #fff;
		font-weight: 500;
		font-family: 'Montserrat', sans-serif;
		@include r(767);
		font-size: 14px; }
	@include r(767) {
		height: 62px;
		font-size: 14px; } }


.sect {
	padding: 70px 0; }

.section-title {
	font-size: 41px;
	margin-bottom: 45px;
	font-family: 'Fira Sans', sans-serif;
	font-weight: 500;
	@include css-lock(20, 41, 600, 1000);
	.text-primary {
		font-weight: 800; } }

.form-row {
	border-radius: 10px;
	overflow: hidden;
	.form-group {
